
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '../../fetch/api'
export default {
    components: {
        XHeader,
        Toast,
        Group,
        XTextarea,
        XInput,
        XButton
    },
    data() {
        return {
            show_success: false,
            show_error: false,
            errorMsg: '',
            value_name: '',
            value_tel: '',
            value_des: ''
        }
    },
    methods: {
        _circle_apply() {
            let data = {
                uid: localStorage.getItem('loginopenid'),
                name: this.value_name,
                describtion: this.value_des,
                phone: this.value_tel,
            }
            console.log(data);
            api.v3_circle_apply(data).then(res => {
                console.log(res)
                if (res.retcode == 200) {
                    this.show_success = true;
                    // this.$router.replace('/home/join')
                } else {
                    this.show_error = true;
                    this.errorMsg = res.errmsg;
                }
            }).catch(error => {
                console.log(error)
            })
        }
    },
}




